<script>
import navbar from '@/components/_navbar'
import footer_component from '@/components/_footer'
import hero from '@/components/_hero'

export default {
    name: 'team_register',
    components: {
        navbar,
        footer_component,
        hero
    },
    data: function(){
        return {
            team: {},
            new_team: {
                name: '',
                email: '',
                password: '',
                reputation: ''
            },
            new_leader: {
                member_name: '',
                member_phone: '',
                member_email: '',
                member_birthdate: '',
                member_gender: '',
                member_job: '',
                member_nationality: '',
                member_country: '',
                member_city: '',
                member_address: '',
                member_zip: ''
            },
            // new_members: [],
            // membersFormCounter: 1,
            // active_contests: [],
            api_url: my_api.url
        }
    },
    mounted: function(){
        this.initiDatepicker()
        this.initiSelect2()
    },
    created: function(){
        // for(let i = 0; i < 100; i++){
        //     this.new_members.push({
        //         member_name: '',
        //         member_phone: '',
        //         member_email: '',
        //         member_birthdate: '',
        //         member_gender: '',
        //         member_job: '',
        //         member_nationality: ''
        //     })
        // }

        // this.$http.get(my_api.url+'/json/activeContests')
        //     .then(res => {
        //         this.active_contests = res.body
        //         console.log(this.active_contests)
        //     })
    },
    methods: {
        saveTeamDetails: function(){
            // validate data
            if(
                !this.new_team.name ||
                !this.new_team.email ||
                !this.new_team.password 
            ){
                this.showError()
            }else{
                $('.team-form-warpper .loader').fadeIn('fast')
                this.$http.post(my_api.url+'/teams/register', this.new_team)
                    .then(res => {
                        if(res.body.success === true){
                            localStorage.setItem('team_id', res.body.team.team_id)
                            this.showSuccess()
                            this.nextStep()
                        }
                    })
                    .catch(err => {
                        console.log('ERROR ', err)
                        this.showError()
                    })
            }
            
        },

        saveLeaderDetails: function(){
            // validate data
            if(
                !this.new_leader.member_name ||
                !this.new_leader.member_phone ||
                !this.new_leader.member_email ||
                !this.new_leader.member_birthdate ||
                !this.new_leader.member_gender ||
                !this.new_leader.member_job ||
                !this.new_leader.member_nationality ||
                !this.new_leader.member_country ||
                !this.new_leader.member_city ||
                !this.new_leader.member_address ||
                !this.new_leader.member_zip 
            ){
                this.showWarning()
            }else{
                $('.team-form-warpper .loader').fadeIn('fast')
                // adding team_id
                this.new_leader.member_team_id = localStorage.getItem('team_id')

                this.$http.post(my_api.url+'/members/register/leader', this.new_leader)
                    .then(res => {
                        if(res.body.success === true) {
                            this.showSuccess()
                            this.nextStep()
                            console.log('leader is done')
                        }
                    })
                    .catch(err => {
                        console.log('ERROR ', err)
                        this.showError()
                    })
            }
            
        },

        auth: function(e){
            e.preventDefault()
            console.log(this.team)
            this.$http.post(this.api_url+'/teams/login', this.team)
                .then(res =>{
                    console.log(res.body)
                    if( res.body.token ){
                        localStorage.setItem('team_token', res.body.token)
                        localStorage.setItem('team_id', res.body.team.id)
                        location.reload()
                        this.$router.push('/profile')
                    }
                })
                .catch(err => {
                    console.log('Error: ', err)
                    console.log('What the hell man!')
                    // this.showError = true;
                    // var self = this;
                    // setTimeout(function(){
                    //     self.showError = false;
                    // }, 2100);
                })
                ;
        },

        // saveMembers: function(){
        //     let self = this;
        //     let newMembersArray = self.new_members.filter(item => {
        //         if(
        //             item.member_name &&
        //             item.member_phone &&
        //             item.member_email &&
        //             item.member_birthdate &&
        //             item.member_gender &&
        //             item.member_job &&
        //             item.member_nationality
        //         ){
        //             item.member_team_id = localStorage.getItem('team_id')
        //             return item
        //         }
        //     })
        //     console.log(newMembersArray)
        //     if( self.membersFormCounter > newMembersArray.length ){
        //         console.log('its a trap!!!')
        //         self.showError()
        //     }else{
        //         console.log('all here boss!')
                
        //         newMembersArray.forEach((item, i) => {
        //             self.$http.post(my_api.url+'/members/register', item)
        //                 .then(res => {
        //                     console.log(res.body)
        //                 })
        //                 .catch(err => {
        //                     console.log('ERROR ', err)
        //                 })
        //             console.log(i, newMembersArray.length)
        //             if(i === (newMembersArray.length - 1)){
        //                 console.log('done', i, (newMembersArray.length - 1))
        //                 self.showSuccess()
        //             }
        //         })
        //     }
            
        // },

        // addMember: function(){
        //     this.membersFormCounter += 1;
        //     this.initiDatepicker()
        // },

        // initing datePicker
        initiDatepicker: function(){
            let self = this;
            let datepickerEl = $('[data-toggle="datepicker"]')
            datepickerEl.datepicker()
            // $('.members-details-form [data-toggle="datepicker"]').on('change', function(e){
            //     let index = $(this).data('object-index')
            //     self.new_members[index].member_birthdate = e.target.value
            // })
            $('.leader-details-form [data-toggle="datepicker"]').on('change', function(e){
                self.new_leader.member_birthdate = e.target.value
            })

            setInterval(() =>{
                let datepickerEl = $('[data-toggle="datepicker"]')
                datepickerEl.datepicker()
                $('.members-details-form [data-toggle="datepicker"]').on('change', function(e){
                    let index = $(this).data('object-index')
                    self.new_members[index].member_birthdate = e.target.value
                })
            }, 1000)
        },

        // initing select2
        initiSelect2: function(){
            let self = this;
            let select_team   = $('.team-details-form [searchable="true"]').select2()
            let select_leader = $('.leader-details-form [searchable="true"]').select2()
            select_team.on('select2:select', function (e) {
                self.new_team.reputation = e.target.value
            });
            select_leader.on('select2:select', function (e) {
                self.new_leader.member_country = e.target.value
            });
        },

        // scroll top
        scrollingTop: function(){
            $('html, body').animate({ scrollTop: ($('.progress').offset().top - ($('#header').height() + 100)) }, 800);
        },

        // Next Step
        nextStep: function(){
            let activeStep = $('.progress .circle.active')
            let activeForm = $('.form-steps.show');

            this.scrollingTop()

            activeStep.next().addClass('fill')
            activeStep.next().next().addClass('active')
            activeStep.removeClass('active').addClass('done')
            setTimeout(function(){
                activeForm.fadeOut('fast', function(){
                    activeForm.removeClass('show').next().addClass('show')
                    activeForm.next().fadeIn('fast')
                })
                activeForm.next().next().addClass('active')
            }, 1500)
            
        },
        
        // Expanding cards
        // expandCard: function(e){
        //     let thisCard = $(e.currentTarget).closest('.member-card')
        //     let thisBtn  = $(e.currentTarget)
        //     thisBtn.toggleClass('plus minus')
        //     thisCard.toggleClass('expanded')
        //     thisCard.find('.card-body').slideToggle('fast')
        // },

        // Alerts
        showError: function(){
            this.scrollingTop()
            $('.team-form-warpper .loader').fadeOut('fast')
            $('.alert-danger').slideDown('fast', function(){
                setTimeout(function(){
                    $('.alert-danger').slideUp('fast')
                }, 2500)
            })
        },
        showWarning: function(){
            this.scrollingTop()
            $('.team-form-warpper .loader').fadeOut('fast')
            $('.alert-warning').slideDown('fast', function(){
                setTimeout(function(){
                    $('.alert-warning').slideUp('fast')
                }, 2500)
            })
        },
        showSuccess: function(){
            this.scrollingTop()
            $('.team-form-warpper .loader').fadeOut('fast')
            $('.alert-success').slideDown('fast', function(){
                setTimeout(function(){
                    $('.alert-success').slideUp('fast')
                }, 2500)
            })
        }
    }
}
</script>

<style scoped>
    .team-register-form .team-form-warpper{
        width: 100%;
        margin: auto;
        position: relative
    }
    .team-register-form .team-form-warpper label{
        margin-top: 15px;
        margin-bottom: 3px;
    }
    .team-register-form .team-form-warpper input{
        margin-top: 0;
    }
    .team-register-form .team-form-warpper button.btn-primary{
        margin-top: 20px
    }
    .team-register-form .team-form-warpper .loader{
        position: absolute;
        right: 0;
        left: 0;
        top: 0;
        bottom: 0;
        background-color: rgba(61, 64, 81, .8);
        display: none;
        z-index: 2;
    }
    .team-register-form .team-form-warpper .loader span{
        width: 70px;
        height: 70px;
        margin: auto;
        border-radius: 50%;
        display: block;
        border: 4px solid #eee;
        border-right-color: #4c5c96;
        position: absolute;
        top: 43%;
        left: 47%;
        transform: rotate(0);
        animation: rotate .3s linear infinite forwards;
    }
    @keyframes rotate{
        100%{
            transform: rotate(360deg);
        }
    }


    
/* Form Progress */
.progress {
  text-align: center;
  margin-bottom: 50px;
}
.progress .circle,
.progress .bar {
  display: inline-block;
  background: #fff;
  width: 100px;
  height: 40px;
  border-radius: 40px;
  border: 1px solid #d5d5da;
  position: relative;
  z-index: 2
}
.progress .bar {
  position: relative;
  width: 60px;
  height: 6px;
  top: -33px;
  margin-left: -5px;
  margin-right: -5px;
  border-left: none;
  border-right: none;
  border-radius: 0;
  z-index: 1
}
.progress .bar:before{
    content: '';
    position: absolute;
    left: 1px;
    top: 0;
    bottom: 0;
    width: 0;
    background: #4c5c96;
    transition: .1s ease-in-out;
}
.progress .bar.fill:before{
    animation: fill 1s forwards ease;
    background: #8bc435;
}
.progress .circle .label {
  display: inline-block;
  width: 32px;
  height: 32px;
  line-height: 32px;
  border-radius: 32px;
  margin: auto;
  margin-top: 3px;
  color: #b5b5ba;
  font-size: 17px;
  display: block;
}
.progress .circle .title {
  color: #b5b5ba;
  font-size: 13px;
  line-height: 30px;
  margin-left: -5px;
}

/* Done / Active */
.progress .bar.done,
.progress .circle.done {
  background: #eee;
}
.progress .bar.active {
  /* background: linear-gradient(to right, #EEE 40%, #FFF 60%); */

}
.progress .circle.done,
.progress .circle.done .label {
  color: #FFF;
  background: #8bc435;
}
.progress .circle.done{
  box-shadow: inset 0 0 2px rgba(0,0,0,.2);
}
.progress .circle.done .title {
  color: #fff;
}
.progress .circle.active,
.progress .circle.active .label {
  color: #FFF;
  background: #4c5c96;
}
.progress .circle.active .title {
  color: #fff;
}

@keyframes fill{
    from{width: 0}
    to{width: 99%}
}


.single-select-contest{
    padding: 10px;
    border: 1px solid rgba(238, 238, 238, .5);
    width: 31%;
    text-align: center;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    transition: .1s;
    cursor: pointer
}
.single-select-contest:hover{
    background: rgba(238, 238, 238, .05);
}
.single-select-contest > *{margin: auto;}
.single-select-contest img.icon{
    width: 30px;
    margin: 20px auto;
}
.single-select-contest h3{
    margin: 20px auto;    
}


.login-form{
    max-width: 100%;
    width: 500px;
    border: 1px solid rgba(238, 238, 238, 0.3);
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
    padding: 20px 10px;
    border-radius: 5px;
    position: relative;
    transform: translate(-50%, 0);
    padding-top: 40px;
}
.login-form .form{
    margin: 0
}
.login-form span.lock{
    position: absolute;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    display: flex;
    top: 0;
    left: 0;
    transform: translate(20%, -50%);
    background: #2e3141;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
    border: 1px solid rgba(238, 238, 238, 0.3)
}
.login-form span.lock i{
    margin: auto;
    color: #fff;
    transform: rotate(90deg);
    font-size: 1.5em
}
.login-form input{
    margin-bottom: 15px;
}
</style>


