<template>
    <div class='page-profile'>
        <navbar></navbar>
        <link href="https://fonts.googleapis.com/icon?family=Material+Icons" rel="stylesheet">
        <!-- Wrapper -->
        <section id="wrapper">
            <section id="upload">
              <div class="inner">
                <h2 class="h-fix">Upload your files</h2>
                <dropzone 
                    id="myVueDropzone" 
                    :url="api_url+'/teams/uploadContestFiles'" 
                    v-on:vdropzone-success="showSuccess"
                    :showRemoveLink="false"
                    :useCustomDropzoneOptions="true"
                    :dropzoneOptions="{
                        uploadMultiple: true, 
                        parallelUploads: 100,
                        headers: {
                            'Authorization': upload_headers.token,
                            'team_id': upload_headers.team_id,
                            'contest_id': upload_headers.contest_id,
                        }
                    }"
                ></dropzone>
              </div>
            </section>
        </section>
    </div>
</template>

<script>
import navbar from '@/components/_navbar'
import hero from '@/components/_hero'
import Dropzone from 'vue2-dropzone'

export default {
    name: 'profile',
    components: {
        Dropzone,
        navbar,
        hero
    },
    data: function(){
        return {
            api_url: my_api.url,
            upload_headers: {}
        }
    },
    created: function(){
        let token = localStorage.getItem('team_token')
        this.upload_headers = {
            'Authorization': token,
            'team_id': localStorage.getItem('team_id'),
            'contest_id': this.getParameterByName('id')
        }
        if(!token){
            this.$router.push('/')
        }

        // console.log( Dropzone )
        // console.log( Dropzone.methods.setOption )
        // Dropzone.methods.setOption({
        //     uploadMultiple: true
        // })
    },
    methods: {
        showSuccess: function (file) {
            console.log('A file was successfully uploaded', file)
        },
        getParameterByName: function(name, url) {
            if (!url) {
                url = window.location.href;
            }
            name = name.replace(/[\[\]]/g, "\\$&");
            var regex = new RegExp("[?&]" + name + "(=([^&#]*)|&|#|$)"),
                results = regex.exec(url);
            if (!results) return null;
            if (!results[2]) return '';
            return decodeURIComponent(results[2].replace(/\+/g, " "));
        },

        // seeData1: function(file){
        //     console.log('seeData1', file)
        // },
        // seeData2: function(file){
        //     console.log('seeData2', file)
        // },
        // seeData3: function(file){
        //     console.log('seeData3', file)
        // },
    }
}
</script>

<style >
#upload{
    max-width: 100%;
    width: 740px;
    margin: 50px auto
}
#myVueDropzone{
    background: transparent;
    border: 2px dashed #eee;
    border-radius: 5px;
}
.dz-default.dz-message span{
    color: #fff;
}
.dz-default.dz-message span i{
    font-size: 30px;
}
.dropzone .dz-preview .dz-error-message{
    left: 30px !important;
    top: 150px !important
}
.vue-dropzone .dz-preview .dz-error-mark, .vue-dropzone .dz-preview .dz-success-mark{
    text-align: center;
}
</style>


