<template>
    <div class='page-login'>
        <navbar></navbar>
        <!-- 
        <hero 
            url="/static/images/wallpapers/login.jpg"
            text="Login"
            icon="/static/images/contest_icon/login.svg"
            ></hero>

        <!-- Wrapper -->
        <section id="wrapper">
          <!-- One -->
          <section class="login-form">
            <span class="lock"><i class="icon ion-key"></i></span>
              <form v-on:submit="auth($event)" class="form">
                <label for="username">Team email</label>
                <input v-model="team.email" type="email" id="username">
                <label for="password">Password</label>
                <input v-model="team.password" type="password" id="password">
                <button type="submit" class="button">Login</button>
              </form>
          </section>
          

        </section>
        <!-- <footer_component></footer_component> -->
    </div>
</template>

<script>
import navbar from './_navbar'
import footer_component from './_footer'
import hero from './_hero'

export default {
    name: 'login',
    components: {
        navbar,
        footer_component,
        hero
    },
    data: function(){
        return {
            team: {},
            api_url: my_api.url
        }
    },
    created: function(){
        localStorage.removeItem('team_token')
        localStorage.removeItem('team_id')
        console.log('Old token removed!')
    },
    methods: {
        auth: function(e){
            e.preventDefault()
            console.log(this.team)
            this.$http.post(this.api_url+'/teams/login', this.team)
                .then(res =>{
                    console.log(res.body)
                    if( res.body.token ){
                        localStorage.setItem('team_token', res.body.token)
                        localStorage.setItem('team_id', res.body.team.custom_id)
                        location.reload()
                        this.$router.push('/profile')
                    }
                })
                .catch(err => {
                    console.log('Error: ', err)
                    console.log('What the hell man!')
                    // this.showError = true;
                    // var self = this;
                    // setTimeout(function(){
                    //     self.showError = false;
                    // }, 2100);
                })
                ;
        }
    }
}
</script>

<style>
.login-form{
    max-width: 100%;
    width: 500px;
    border: 1px solid rgba(238, 238, 238, 0.3);
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
    padding: 20px 10px;
    border-radius: 5px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -90%);
    padding-top: 40px;
}
.login-form .form{
    margin: 0
}
.login-form span.lock{
    position: absolute;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    display: flex;
    top: 0;
    left: 0;
    transform: translate(20%, -50%);
    background: #2e3141;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
    border: 1px solid rgba(238, 238, 238, 0.3)
}
.login-form span.lock i{
    margin: auto;
    color: #fff;
    transform: rotate(90deg);
    font-size: 1.5em
}
.login-form input{
    margin-bottom: 15px;
}
</style>


