<template>
    <div class='page-single_contest'>
        <navbar></navbar>
        <hero 
            :url="api_url + '/uploads/contests/' + this_contest_main_image"
            :icon="api_url + '/uploads/contests/' + this_contest_main_icon"
            :date="this_contest.end_date | isExpired"
            :text="this_contest.title"
            :description="this_contest.short_brife"
            btnText="Upload your files now"
            :btnUrl="'/upload?id='+this_contest._id"
            v-if="team_logged_in"
            ></hero>
        <hero 
            :url="api_url + '/uploads/contests/' + this_contest_main_image"
            :icon="api_url + '/uploads/contests/' + this_contest_main_icon"
            :date="this_contest.end_date | isExpired"
            :text="this_contest.title"
            :description="this_contest.short_brife"
            btnText="Login"
            btnUrl="/login"
            v-else
            ></hero>

        <!-- Wrapper -->
        <section id="wrapper">
          <section id="contest_desc" class="wrapper style1">
              <h2 class="h-fix">brief</h2>          
              <div class="inner" v-html="this_contest.brief"></div>
          </section>
          
          <section id="contest_slider" class="wrapper alt style2">
            <h2 class="h-fix">Media</h2>
            <div class="inner">
                <a :href="api_url+'/uploads/contests/'+image.filename" data-lightbox="image-1" class="single_image" v-for="image in this_contest.slider_images" :style="'background-image: url('+api_url+'/uploads/contests/'+image.filename+')'"></a>
            </div>
            <div class="inner">
                <div 
                    class="video-warpper" 
                    :data-url="this_contest.youtube_video">
                    <div class="dark-layer">
                        <img src="https://www.youtube.com/yt/brand/media/image/YouTube-icon-full_color.png" class="play-btn" alt="Play Button" />
                    </div>
                </div>
            </div>
          </section>

          <section id="contest_prizes" class="wrapper style3">
            <h2 class="h-fix">Prizes</h2>
            <div class="inner">
                <div class="prizes">

                    <div class="prize" v-for="item in this_contest.prizes">
                        <div class="cash">{{item.prize_cash}}$</div>
                        <div class="title">{{item.prize_st}}</div>
                        <div class="decription">{{item.prize_details}}</div>
                    </div>

                </div>
            </div>
          </section>

          <section id="contest_download" class="wrapper alt style4">
            <h2 class="h-fix">Download</h2>
            <div class="inner">

                <div class="download-card" v-for="item in this_contest.contest_files">
                    <div class="file-name">{{item.originalname}}</div>
                    <div class="file-size">{{item.size | fileSize }}</div>
                    <a download :href="api_url+'/uploads/contests/'+item.filename"><i class="icon ion-ios-cloud-download-outline"></i></a>
                </div>

            </div>
          </section>

          <section id="contest_jury" class="wrapper style5">
            <h2 class="h-fix">Jury</h2>
            <div class="inner">
                <div 
                    class="judge-card"
                    v-for="item in judges"
                    :class="{'hovered': item.judge_brief}"
                    :style="'background-image: url('+api_url+'/uploads/judges/'+item.judge_avatar.filename+')'"
                    >
                    <div class="info">
                        <div class="name">{{item.judge_username}}</div>
                        <div class="job" v-if="item.judge_brief">{{item.judge_brief}}</div>
                    </div>
                </div>

            </div>
          </section>

          <section id="contest_calendar" class="wrapper alt style6">
            <h2 class="h-fix">FAQ</h2>
            <div class="inner">
                <div class="faqs">
                    <div class="single-faq" v-for="item in this_contest.faq">
                        <div class="head" @click="toggleFaq($event)">
                            <div class="text">{{item.faq_q}}</div>
                            <span class="icon ion-plus-round"></span>
                            <span class="icon ion-minus-round"></span>
                        </div>
                        <div class="body">{{item.faq_a}}</div>
                    </div>
                </div>
            </div>
            <div class="someHeightPlease"></div>
            <h2 class="h-fix">Calendar</h2>
            <div class="inner">
                <div class="single_calendar" v-for="item in this_contest.calendar">
                    <div class="start-date">
                        {{item.calendar_start_date | getMonthandDay}}
                        <span>{{item.calendar_start_date | getYear}}</span>
                    </div>
                    <div class="title">
                        {{item.calendar_title}}
                        <span class="fee">{{item.calendar_fee}}</span>
                    </div>
                    <div class="end-date">
                        {{item.calendar_end_date | getMonthandDay}}
                        <span>{{item.calendar_end_date | getYear}}</span>
                    </div>
                </div>

            </div>
          </section>
            

          <section id="contest_partners" class="wrapper style1">
            <h2 class="h-fix">Partners</h2>
            <div class="inner">
                <ul class="partners">
                  <li v-for="item in partners" v-if="item">
                    <a :href="item.link" target="_blank">
                        <img :src="api_url+'/uploads/partners/'+item.logo.filename" alt="">
                    </a>
                  </li>
              </ul>
            </div>
          </section>
        
          

        </section>
        <footer_component></footer_component>
    </div>
</template>

<script>
import navbar from './_navbar'
import footer_component from './_footer'
import hero from './_hero'

export default {
    name: 'single_contest',
    components: {
        navbar,
        footer_component,
        hero
    },
    data: function(){
        return {
            this_contest: {},
            this_contest_main_image: '',
            this_contest_main_icon: '',
            judges: {},
            partners: [],
            api_url: my_api.url,
            team_logged_in: false
        }
    },
    created: function(){
        let team_token = localStorage.getItem('team_token')
        if(team_token){
            this.team_logged_in = true
        }
        var id = this.getParameterByName('id')
        this.$http.get(my_api.url + '/json/contest/'+id)
            .then(res => {
                this.this_contest = res.body
                this.this_contest_main_image = res.body.main_image[0].filename,
                this.this_contest_main_icon = res.body.icon[0].filename,
                window.document.title =  this.this_contest.title

                // get partners
                this.this_contest.partners.forEach((item, i)=> {
                    this.$http.get(my_api.url + '/json/getContestPartners/'+item)
                        .then(res => {
                            this.partners.push(res.body)
                        })
                })
                
            })
        // get judges
        this.$http.get(my_api.url + '/json/getContestJudges/'+id)
            .then(res => {
                this.judges = res.body
            })
        
        
    },
    mounted: function(){
        // Append the video thumbnail on load
        let self = this
        setTimeout(function(){
            $('.video-warpper').is(function(){
                var id = self.get_video_id($(this).data('url')) // Video ID
                $(this).append( '<img class="video-thump" src="https://img.youtube.com/vi/'+ id +'/maxresdefault.jpg" alt="" />' ); // Append the image
            });
            // Append the video iframe on user's click on the thumbnail
            $('.video-warpper').click(function(){
                var id = self.get_video_id($(this).data('url')) // Get the ID
                $(this).append(
                    '<iframe src="https://www.youtube.com/embed/' + id + '?showinfo=0&iv_load_policy=3&modestbranding=1&autoplay=1&rel=0"></iframe>'); // Appending the iframe
                $(this).find('.dark-layer').fadeOut() // Remove the dark layer
                $(this).find('.video-thump').fadeOut() // Remove the video thumbnail
            });
        }, 500)
    },
    methods: {
        getParameterByName: function(name, url) {
            if (!url) {
                url = window.location.href;
            }
            name = name.replace(/[\[\]]/g, "\\$&");
            var regex = new RegExp("[?&]" + name + "(=([^&#]*)|&|#|$)"),
                results = regex.exec(url);
            if (!results) return null;
            if (!results[2]) return '';
            return decodeURIComponent(results[2].replace(/\+/g, " "));
        },
        get_video_id: function(url){
            var regExp = /^.*((youtu.be\/)|(v\/)|(\/u\/\w\/)|(embed\/)|(watch\?))\??v?=?([^#\&\?]*).*/;
            var match = url.match(regExp);
            return (match&&match[7].length==11)? match[7] : false;
        },
        toggleFaq: function(e){
            let thisFaq = $(e.target)
            thisFaq.closest('.single-faq').toggleClass('active')
        }
    },
    filters: {
        getMonthandDay: function(value) {
            return moment(value, 'YYYY-MM-DD').format('DD MMMM');
        },
        getYear: function(value) {
            return moment(value, 'YYYY-MM-DD').format('YYYY');
        },
        fileSize: function(value) {
            let kb = Math.floor(value / 1024);
            if( kb > 1023 ){
                return Math.floor(kb / 1024) + 'mb'
            }else{
                return kb + 'kb'
            }
        },
        isExpired: function(value) {
            let date = new Date(value)
            let now = new Date()
            

            if(date >= now) {
                return value
            } else {
                return 'Expired'                
            }
        }
    }
}
</script>

<style>
    #contest_slider .inner {
        display: flex;
        flex-wrap: wrap;
    }
    #contest_slider .single_image{
        width: 33.333%;
        height: 200px;
        margin: auto;
        background-position: center center;
        background-repeat: no-repeat;
        background-size: cover;
        border: 0;
        margin-left: 0;
        margin-right: 0;
    }
    .video-warpper {
        background: #111;
        text-align: center;
        position: relative;
        overflow: hidden;
        width: 100%;
        height: 600px;
        margin: 30px auto;
        display: block;
        box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
    }
    .video-warpper:hover {
        cursor: pointer;
    }
    .video-warpper:hover .dark-layer {
        background: rgba(9, 9, 9, 0.4);
    }
    .video-warpper:hover .dark-layer img.play-btn {
        opacity: 1;
    }
    .video-warpper img {
        position: relative;
        height: 100%;
        width: 100%;
        z-index: 1;
    }
    .video-warpper iframe {
        width: 100%;
        height: 600px;
        border: none;
    }
    .video-warpper .dark-layer {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(9, 9, 9, 0.3);
        z-index: 2;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-transition: 0.15s ease-in-out;
        transition: 0.15s ease-in-out;
    }
    .video-warpper .dark-layer img.play-btn {
        width: 100px;
        height: 60px;
        margin: auto;
        opacity: 0.95;
        -webkit-transition: 0.15s ease-in-out;
        transition: 0.15s ease-in-out;
    }
    .h-fix {
        padding-bottom: 20px;
        text-align: center;
        font-size: 2em;
        text-transform: uppercase;
    }
    .inner {
        padding-top: 0 !important;
    }
    #contest_prizes .prize {
        box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
        padding: 10px 15px;
        background: #2e3141;
        border-radius: 5px;
        position: relative;
        padding-top: 25px;
        display: flex;
        user-select: none;
        cursor: default;
        margin-bottom: 40px;
    }
    #contest_prizes .prize .title {
        box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
        position: absolute;
        left: 10px;
        top: -20px;
        padding: 5px 10px;
        border-radius: 5px;
        background: #4c5c96;
    }    
    #contest_prizes .prize .cash {
        font-size: 2em;
        margin: auto;
        padding: 5px 10px;
        font-weight: bold;
    }    
    #contest_prizes .prize .decription {
        margin: auto;
        width: 80%;
    }

    #contest_download .download-card {
        box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
        padding: 15px 25px;
        background: #2e3141;
        border-radius: 5px;
        position: relative;
        display: flex;
        flex-direction: column;
        margin-bottom: 15px;
    }
    #contest_download .download-card .file-name {
        margin: auto;
        font-size: 1.2em;
        text-transform: uppercase;
        margin-left: 0;
    }
    #contest_download .download-card .file-size {
        font-size: 14px;
        margin: auto;
        margin-left: 0;
        opacity: .6
    }    
    #contest_download .download-card a {
        box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
        position: absolute;
        right: 20px;
        top: 50%;
        transform: translate(0, -50%);
        padding: 5px 15px;
        border-radius: 5px;
        background: rgba(76, 92, 150, 1);
        border: 0;
        font-size: 22px;
        transition: .1s;        
    }
    #contest_download .download-card a:hover {
        background: rgba(76, 92, 150, .8);
    }    

    #contest_jury .inner {
        display: flex;
        flex-wrap: wrap;
    }
    #contest_jury .judge-card {
        box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
        border-radius: 5px;
        width: 31.333%;
        margin: auto;
        margin-bottom: 15px;
        border: 0;
        margin-left: 0;
        height: 400px;
        background-position: center center;
        background-repeat: no-repeat;
        background-size: cover;
        position: relative;
        margin-right: 15px;
        overflow: hidden;
    }
    #contest_jury .judge-card .info {
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        padding: 10px;
        text-align: center;
        border-radius: 0 0 5px 5px;
        background: rgba(76, 92, 150, .7);
        top: 88%;
        transition: .2s
    }    
    #contest_jury .judge-card.hovered:hover .info{
        top: 60%
    }    
    #contest_jury .judge-card .info .name {
        font-size: 1.2em;
    }    
    #contest_jury .judge-card .info .job {
        font-size: .8em;
    }
                    
    .single_calendar{
        box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
        padding: 15px 25px;
        background: #2e3141;
        border-radius: 5px;
        position: relative;
        margin-top: 15px;
        display: flex;
        flex-direction: row
    }
    .single_calendar > div{
        margin: auto
    }    
    .single_calendar > div.start-date,
    .single_calendar > div.end-date {
        box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
        position: absolute;
        right: 20px;
        top: 50%;
        transform: translate(0, -50%);
        padding: 5px 15px;
        border-radius: 5px;
        background: rgba(76, 92, 150, 1);
        border: 0;
        font-size: 18px;
        transition: .1s;
        text-align: center;
    }   
    .single_calendar > div.start-date {
        right: auto;
        left: 20px;
    }
    .single_calendar > div.start-date span,
    .single_calendar > div.end-date span {
        font-size: 1.2em;
        display: block;
        letter-spacing: 4px
    }
    .single_calendar .title {
        font-size: 1.3em;
        font-weight: bold;
        text-align: center;
        text-transform: uppercase
    }
    .single_calendar .title .fee{
        display: block;
        font-weight: 100;
        font-size: .6em
    }

    .faqs{
        box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
        border-radius: 5px;
        position: relative;
        margin-top: 15px;
        overflow: hidden
    }
    .single-faq{
        border-bottom: 1px solid rgba(238, 238, 238, .3);
        cursor: pointer;
    }
    .single-faq:last-of-type{
        border: 0;
    }    
    .single-faq.active .body{
        height: auto;
        padding: 15px 25px;        
    }
    .single-faq.active .head{
        background: rgba(46, 49, 65, .8);
    }    
    .single-faq.active .head .ion-minus-round{
        display: block;
    }
    .single-faq.active .head .ion-plus-round{
        display: none
    }
    .single-faq .head{
        padding: 15px 25px;
        background: #2e3141;
        display: flex;
        transition: .2s;
        justify-content: space-between;
    }
    .single-faq .head:hover {
        background: rgba(46, 49, 65, .8);
    }  
    .single-faq .head .ion-minus-round{
        display: none
    }
    .single-faq .body{
        background: rgba(46, 49, 65, .7);
        transition: .2s;
        height: 0;
        padding: 0 25px;
        overflow: hidden
    }
    .someHeightPlease{padding: 30px 0}
</style>


