<template>
    <!-- Banner -->
    <div id="scene">
        <section id="banner" class="layer" data-depth="0.20">
            <div class="inner">
                <div class="contest_logo" v-if="icon">
                    <img :src="icon">
                </div>
                <h2>{{text}} <span class="expire-date" v-if="date">Deadline: {{date | date}}</span></h2>
                <p v-if="description">
                    {{description}}
                </p>

                <router-link
                    v-if="btnUrl && (date | date !== 'Expired')"
                    class="btn btn-default btn-banner-apply"
                    tag="button"
                    :to="btnUrl"
                    >
                    {{btnText}}</router-link>
            </div>
        </section>
        <div 
            class="layer" 
            data-depth="0.10" 
            id="bannerWallpaper" 
            :style="'background-image: url('+url+')'"
            ></div>
    </div>
</template>

<script>
export default {
    name: 'hero',
    props: ['url', 'icon', 'text', 'date', 'description', 'btnText', 'btnUrl'],
    mounted: function(){
        console.log('run parallax')
        var scene = document.getElementById('scene');
        if(scene){
            var parallax = new Parallax(scene);
        }
    },
    filters: {
        date: function(value){
            if( moment(value, 'YYYY-MM-DD').format('DD/MMMM/YYYY') === 'Invalid date'){
                return 'Expired'
            }else{
                return moment(value, 'YYYY-MM-DD').format('DD/MMMM/YYYY') 
            }
        },
    }
}
</script>

<style scoped>
    .expire-date{
        display: block;
        font-size: 16px;
    }
    #bannerWallpaper::after{
        background-color: rgba(0, 0, 0, 0.4);
    }
</style>


