<template>
  <div class="part-navbar">
    <!-- Header -->
    <header id="header" class="alt">
        <h1><a href="#">Grintos</a></h1>
        <nav>
            <a href="#menu">Menu</a>
        </nav>
    </header>

    <!-- Menu -->
    <nav id="menu">
        <div class="inner">
            <h2>Menu</h2>
            <ul class="links">
                <li><router-link to="/" exact>
                    Home
                    <i class="icon ion-home"></i>
                </router-link> </li>
                <li v-if="token"><router-link to="/profile" exact>
                    Profile
                    <i class="icon ion-person-stalker"></i>
                </router-link> </li>
                <li v-if="token"><router-link to="/logout" exact>
                    Logout
                    <i class="icon ion-person-stalker"></i>
                </router-link> </li>
                <li v-if="!token"><router-link to="/login" exact>
                    Login
                    <i class="icon ion-person-stalker"></i>
                </router-link> </li>
                <li v-if="!token"><router-link to="/team_register" exact>
                    Register Team
                    <i class="icon ion-person-stalker"></i>
                </router-link> </li>
            </ul>
            <a href="#" class="close">Close</a>
        </div>
    </nav>
  </div>
</template>

<script>
export default {
  name: 'navbar',
  data () {
    return {
      token: ''
    }
  },
  created: function(){
      this.token = localStorage.getItem('team_token')
        // if(!token){
        //     this.$router.push('/')
        // }else{

        // }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
ul.links{
    text-align: left;
}
ul.links a{
    padding-left: 10px !important;
    padding-right: 10px !important;
}
ul.links a i{
    float: right;
    font-size: 22px
}
</style>



